html {
  // font-family: Helvetica, Arial, sans-serif;
  font-family: "Noto Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 300; // 100-900
  font-style: normal;
  font-variation-settings: "wdth" 100;
}

body {
  width: 80%;
  margin: 2% auto;
}

.masthead {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 20px;
  margin: 2% 0%;
  gap: 10px;
  // border: solid black;
}

.title {
  flex: 4;
  font-size: 1.25em;
  font-weight: 500;
  // border: solid black;
  // justify-content: flex-end;
}

.topnav {
  flex: 1;
  // justify-content: flex-end;
  // border: solid black;
}

.topnav.item {
  width: fit-content;
  margin-right: 10px;
  // border: solid black;
}

.topnav.item.selected,
.masthead a:hover,
.work.thumb:hover {
  color: white;
  background-color: rgb(50, 50, 50);
}

.works {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 10px;
  // border: solid black;
}

.work.thumb {
  object-fit: cover;
  width: 75%;
  height: auto;
  // border: solid black;
}

.work.thumb.title {
  font-size: 1em;
  margin-top: 0px;
  font-weight: 200;
  width: fit-content;
}

ul {
  // list-style-type: square;
  list-style-type: "-    ";
  list-style-position: outside;

  // list-style-type: none;
  // margin: 0px;
  // padding: 0px;
}

a {
  color: black;
  text-decoration: none;
}
